html {
  font-size: 62.5%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  min-height: 100vh;
  font-family: sans-serif;
  overflow: hidden;
  background: black;
  color: white;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.cover {
  width: 30%;
  margin: 0 auto;
  height: 30vh;
  object-fit: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    height: 100%;
  }
}

.track-info {
  width: 30%;
  margin: 2rem auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  pointer-events: none;
  span {
    display: none;
  }
  h4 {
    font-size: 2rem;
  }
  p {
    font-size: 1.7rem;
  }
}

.icon {
  cursor: pointer;
  width: 3rem;
  height: 3rem;
  font-size: 2.5rem;
  background: transparent;
  color: #c800ff;
  z-index: 1;
  transition: 0.3s ease;
  pointer-events: none;
  user-select: none;
  svg {
    width: 3rem;
    height: 3rem;
    font-size: 2.5rem;
  }
}

.icon.mute,
.icon.loop {
  color: #ccc;
}

.container {
  width: 100%;
  height: 85vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  padding-top: 8vh;
}

.btn {
  margin-right: 1rem;
  margin-left: 1rem;
  padding: 0.5rem 1.5rem;
  width: 16rem;
  height: 4.5rem;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  font-size: 1.8rem;
  justify-content: center;
  transition: 0.3s ease;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  padding: 2rem 3rem;
  border: 2px solid rgba(211, 97, 243, 0.5);
  background: transparent;
  margin: 1.5rem 1rem;
  cursor: pointer;
  color: white;
  border-radius: 0.5rem;
}

@import "./nav";
@import "./player";
@import "./tracksContainer";
@import "./libraryContainer";
@import "./loading";
@import "./_addForm";
@import "./_alert";
@import "./landing";
